@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}


.header{
    padding: 20px 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.logo--dark{
    background-image: url(/Gifos-react-app/static/media/logo-mobile-modo-noct.47e214b1.svg);
}

.logo--light{
    background-image: url(/Gifos-react-app/static/media/logo-desktop.2f122678.svg);
}

@media (max-width:767px){
    .logo{
        width: 40px;
        height: 40px;
    }
}


.btn{
    font-size: 14px;
    font-weight: 700;
    padding: 12px 30px;
    background-color: transparent;
    border-radius: 60px;
    cursor: pointer;
}
.btn:disabled{
    background-color: #110322;
    cursor: not-allowed;
}

.btn--light{
    border: 1px solid #6809E1;
    color: #6809E1;
}

.btn--light:hover{
    background-color: #50E3C2;
    border: 1px solid #50E3C2;
}

.btn--dark{
    border: 1px solid #F3F3F3;
    color: #F3F3F3;
}

@media (max-width:767px){
    .btn{
        font-size: 10px;
        padding: 8px 12px;
    }
}
.search{
    display: block;
    text-align: center;
    padding: 60px 0;
}

.search__title{
    font-weight: 400;
    font-size: 32px;
    margin: 5px 0;
}

.search__title--dark{
    color: #f3f3f3;
}

.search__title--light{
    color: #572EE5;
    ;
}


@media (max-width:767px){
    .search__title{
        font-size: 25px;
        margin: 5px 0;
    }
}
.search__input{
    margin: 0 auto;
    position: relative;
}

.search__input--container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -5px;
}

.search__input--container input{
    min-width: 456px;
    min-height: 52px;
    border: 1px solid black;
    border-radius: 60px 0 0 60px;
    padding-left: 20px;
    font-size: 18px;
    outline: none;
    background-color: transparent;
}

.search__input--container .input--dark{
    border: 1px solid white;
    color: white;
}

.search__input--container ::-webkit-input-placeholder{
    font-weight: 100;
    font-size: 18px;
}

.search__input--container :-ms-input-placeholder{
    font-weight: 100;
    font-size: 18px;
}

.search__input--container ::placeholder{
    font-weight: 100;
    font-size: 18px;
}

.search__input--container button{
    min-width: 86px;
    min-height: 52px;
    border: 1px solid #572EE5;
    border-radius: 0 60px 60px 0;
    background-color: #572EE5;
}

.icon{
    display: inline-block;
    width: 20px;
    height: 20px;
    background-image: url(/Gifos-react-app/static/media/icon-search-mod-noc.ecc2b197.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

@media (max-width:767px){
    .search__input--container input{
        min-width: 250px;
    }
    .search__input img{
        width: 250px;
    }
}

.result{
    text-align: center;
}

.subtitle--dark{
    color: white;
}

.subtitle--light{
    color: #572EE5;
}

.gif__container{
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 15px;
    grid-gap: 15px;
    gap: 15px;
}

.gif__container--light{
    background-color: #E7E7E7;
}

.gif__container--dark{
    background-color: #212121;
}
.gif__link{
    width: 32%;
    cursor: pointer;
}

.gif__image{
    width: 100%;
    height: 300px;
    object-fit: cover;
}

@media (max-width:1200px){
    .gif__link{
        width: 48%;
    }
}

@media (max-width:767px){
    .gif__link{
        width: 100%;
    }
}


.search__list{
    position: absolute;
    width: 550px;
    top: 240px;
    border: 1px solid #572EE5;
    border-radius: 10px;
    background-color: white;
    object-fit: cover;
    overflow: hidden;
    display: none;
}
.visible{
    display: block;
}
.search__list-item{
    width: 100%;
    border-bottom: 1px solid whitesmoke;
    text-align: left;
    padding: 10px;
    font-weight: 100;
    cursor: pointer;
}
.search__list-item:hover{
    background-color: whitesmoke;
}
.search__list-icon{
    display: inline-block;
    width: 20px;
    height: 20px;
    background-image: url(/Gifos-react-app/static/media/icon-search.657bf939.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-right: 10px;
}
@media (max-width:767px){
    .search__list{
        width: 300px;
        top: 175px;
    }
}

.app{
  max-width: 100%;
  margin: 0 auto;
}

.app--light{
  background-color: #fff;
}

.app--dark{
  background-color: #373737;
}

.container{
  max-width: 70%;
  margin: 0 auto;
}


@media (max-width:767px){
  .container{
    max-width: 90%;
  }
}
